@import "../utils/mixins";

.log-in {
    width: 100%;
    background-color: $white;

    &__content {
        padding: 20px;
    }

    &__avatar {
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        width: 90px;
        height: 90px;
        background-color: $primary-color;
        overflow: hidden;
        // To make overflow clipping work on transform animation below.
        transform: translateZ(0);

        img {
            max-width: 90px;
            max-height: 90px;
            // Animate out: easeInQuad.
            transform: translateY(90px);
            transition: transform 0.5s 0s cubic-bezier(0.55, 0.085, 0.68, 0.53);

            .log-in-open &,
            .utility-nav__item:hover .log-in--large &,
            .utility-nav__item:focus .log-in--large &,
            .utility-nav__item.is-active .log-in--large & {
                // Animate in: easeOutExpo.
                transition: transform 0.5s 0.4s cubic-bezier(0.19, 1, 0.22, 1);
                transform: translateY(0);
            }
        }
    }

    &__header {
        @extend .font-weight--regular;
        @include font-size(2);
        color: $grey;
        text-align: center;
    }

    &__links {
        max-width: 350px;
        margin: 0 auto;
    }
}

@include mq($from: tablet) {
    .log-in--small {
        display: none;
    }

    .log-in--large {
        @include ensure-at-front;
        position: absolute;
        visibility: hidden;
        top: 100%;
        top: calc(100% + 1px);
        right: 0;
        width: 300px;
        cursor: default;
        box-shadow: 0 0 20px -2px $dropshadow-color;

        .log-in__forgotten {
            padding: 10px 0;
        }

        .log-in__links a {
            padding: 0;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .log-in-open & {
            display: block;
        }
    }

    .utility-nav__item:hover .log-in--large,
    .log-in-open .log-in--large {
        visibility: visible;
    }
}

// Mobile toggle.
@include mq($until: tablet) {
    .log-in {
        max-height: 0;
        overflow: hidden;
        transition: max-height $delay--long ease;

        .log-in-open & {
            max-height: 750px;
        }
    }
}
